import React, { useEffect, useState } from "react";
import axios from "../../axios";
import {
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Table,
  InputLabel,
  MenuItem,
  FormControl,
  Select,
  Stack,
} from "@mui/material";

import { useSelector } from "react-redux";
function ListDepartments() {
  const collegeID = useSelector((state) => state.collegeID);
  const [rows, setRows] = useState([]);
  const [facultyList, setFacultytList] = useState([]);
  const [facultyId, setFacultyId] = useState("");
  const handleChange = (event) => {
    setFacultyId(event.target.value);
    setRows([]);
    axios
      .get("api/departments/" + event.target.value + "/" + collegeID, {})
      .then((res) => {
        setRows(res.data);
      })
      .catch((error) => {
        alert(error);
        console.log(error);
      });
  };
  useEffect(() => {
    axios
      .get("api/departments/faculty/" + collegeID)
      .then((res) => {
        if (res.status === 200) {
          setFacultytList(res.data);
        }
      })
      .catch(function (error) {
        alert("Error occured:" + error);
      });
  }, []);
  return (
    <Stack sx={{ width: "100%" }} spacing={5}>
      <FormControl fullWidth>
        <InputLabel id="demo-simple-select-label">Faculty / School</InputLabel>
        <Select
          labelId="demo-simple-select-label"
          id="demo-simple-select"
          value={facultyId}
          label="Faculty / School "
          onChange={handleChange}
        >
          {facultyList.map((faculty, index) => (
            <MenuItem key={index} value={faculty.Faculty_ID}>
              {faculty.Faculty_Name}
            </MenuItem>
          ))}
        </Select>
      </FormControl>
      <TableContainer component={Paper}>
        <Table aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell
                style={{ fontWeight: "bold", textTransform: "uppercase" }}
              >
                S.No
              </TableCell>
              <TableCell
                style={{ fontWeight: "bold", textTransform: "uppercase" }}
              >
                Department Name
              </TableCell>
              <TableCell
                style={{ fontWeight: "bold", textTransform: "uppercase" }}
              >
                Year of Establishment
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map((row, index) => (
              <TableRow key={row.Faculty_ID}>
                <TableCell align="left">{index + 1}</TableCell>
                <TableCell align="left">{row.Department_Name}</TableCell>
                <TableCell align="left">{row.Dept_Est_Year}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </Stack>
  );
}

export default ListDepartments;
